.image{
  position: relative;
  height:200px;
  width:770px;
/* IE */
}
.nome{
  transform: rotate(-2deg);
  z-index: 2;
  position: absolute;
  top: 1%;
  left: 0;
  right: 0;
  font-size: 31px;
  width: 100%;
  margin-left:3%;
  height: 26px;
  margin-top: 1.5%; /* 1/2 height */
  color:white;
}
.image img{
  z-index: 1;
}

body {
  background-color: transparent;
}

.mensagem{
  z-index: 2;
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
  font-size: 16px;
  width: 100%;
  margin-left:4%;
  height: 26px;
  margin-top: 7%; /* 1/2 height */
  color:white;
}